import Vue from 'vue'
import Router from 'vue-router'
import Check from '@/components/Check'
import Add from '@/components/Add'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
  	{
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/check',
      name: 'Check',
      component: Check
    },
    {
      path: '/add',
      name: 'Add',
      component: Add
    },
    {
  	  path: '/score-enter', redirect: '/'
	}
  ]
})



