<template>
  <div id="app">
    <h1>学生成绩录入系统</h1>
    <img src="./assets/logo.png">
  <p>
    <!-- 使用 router-link 组件来导航. -->
    <!-- 通过传入 `to` 属性指定链接. -->
    <!-- <router-link> 默认会被渲染成一个 `<a>` 标签 -->
    <router-link class="hashSelect" to="/Home">HOME</router-link>
    <router-link class="hashSelect" to="/Add">ADD</router-link>
    <router-link class="hashSelect" to="/Check">CHECK</router-link>
  </p>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
#app {
  width: 40%;
  margin: 0 auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.hashSelect {
  padding: 0.5rem;
  text-decoration: none;
  background: #999;
}

@media only screen and (min-width: 768px) and (max-width: 1024px){
  #app {
    width: 80%;
    font-size: 1.5rem;
  }
}
</style>



