<template>
  <div class="add">
    <h1>{{ msg }}</h1>
    <ul class="courses">
        <li v-for="course in courses" @click="setCBgColor(course)" :class="{selected: course.selected}">{{ course.name }}</li>
    </ul>
    <ul class="students">
      <li v-for="student in students" @click="setBgColor(student)" :class="{selected: student.selected}">{{ student.name }}</li>
    </ul>
    <input type="text" placeholder="enter score" @keyup.enter="showSuccess" v-model="score">
    <p>添加分数： {{ score }}</p>
  </div>
</template>

<script>
import courses from '../js/store.js';
export default {
  name: 'Add',
  data () {
    return {
      msg: 'ADD',
      score: '',
      selectedStudent: '',
      selectedCourse: '',
      courses: courses.store.fetch(),
      students: [
        {
          name: "学生甲",
          selected: false,
        },
        {
          name: "学生乙",
          selected: false,
        },
        {
          name: "学生丙",
          selected: false,
        },
        {
          name: "学生丁",
          selected: false,
        },
        {
          name: "学生戊",
          selected: false,
        },
      ]
    }
  },
  methods: {
    setBgColor(x) {
      this.students.map(x => x.selected = false);
      x.selected = true;
    },
    setCBgColor(x) {
      this.courses.map(x => x.selected = false);
      x.selected = true;
    },
    showSuccess() {
      courses.store.save(this.courses)

      this.students.map( x => {
        if(x.selected === true) {
          this.selectedStudent = x.name;
          x.selected = false;
        }
      });
      let student = this.selectedStudent;
      this.courses.map( x => {
        if(x.selected === true) {
          this.selectedCourse = x.name;
          x.selected = false;
        }
      });
      let course = this.selectedCourse;
      let courseItem = null;
      for(let i of this.courses) {
        if(i.name === course) {
          courseItem = i.scores;
        }
      }
      for(let i in courseItem) {
        if(i === student) {
          courseItem[i] = this.score;
        }
      }
      alert("添加成功");
      this.score = '';
    }
  },
  watch: {   //监听items，比每次使用addNew就插入数据好
    courses: {
      /*handler(val, oldVal) {
        Store.save(val)
        console.log(val, oldVal);*/
        handler(items) {
          courses.store.save(items)
      },
      deep: true  //深拷贝差不多，监听的数据如果是引用类型，要使用deep属性才能监听到修改
    }
  },  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.courses {
  width: 50%;
  float: left;
}

.students {
  width: 50%;
  float: right;
}

li {
  padding: 1rem;
  background: rgba(99,55,33,.5);
  text-align: left;
  cursor: pointer;
}

input {
  width: 100%;
  box-sizing: border-box;
  margin-top: .5rem;
  padding: .5rem;
}

.selected {
  background: rgba(33,33,33,.2);
}

a {
  color: #42b983;
}

</style>



