<template>
  <div class="check">
  <h1>{{ msg }}</h1>
  <ul class="courses">
        <li v-for="course in courses" @click="setCBgColor(course)" :class="{selected: course.selected}">{{ course.name }}</li>
  </ul>
  <br>
  <ul class="show">
    <li v-for="(value, key) in scores">{{ key }},{{ value }}</li>
  </ul>
  <p v-show="Object.keys(scores).length">平均分：{{ ave }}</p>
  </div>
</template>

<script>
import courses from '../js/store.js';
export default {
  name: 'Check',
  data() {
    return {
      msg: 'CHECK',
      scores: {},
      ave: '',
      courses: courses.store.fetch(),
    }
  },
  methods: {
    showAve() {
      this.scores === {} ? false : true;
    },
    setCBgColor(x) {
      this.courses.map(x => x.selected = false);
      x.selected = true;
      this.scores = x.scores;
      let sum = 0;
      for(let x in this.scores) {
        sum += Number.parseInt(this.scores[x]);
      }
      this.ave = sum/5;
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.selected {
  background: rgba(33,33,33,.2);
}

li {
  padding: 1rem;
  background: rgba(99,55,33,.5);
  text-align: left;
  cursor: pointer;
}

a {
  color: #42b983;
}

.show li {
  background: #fff;
  padding: 0;
}

</style>



